.chat-wrapper {
  display: flex;
}

.chats-sidebar {
  width: 310px;
  background-color: var(--color-light-bg);
  height: 100vh;
  border-right: 1px solid var(--color-dark-bg);
  flex-direction: column;
  flex-shrink: 0;
  display: inline-flex;
}

.chat-profile {
  color: var(--color-text);
  align-items: center;
  padding: 1rem 1rem 0;
  font-size: 1.4rem;
  display: flex;
}

.chat-profile__img {
  width: 32px;
  height: 32px;
  margin-right: 1rem;
}

.chat-search {
  background: var(--color-dark-bg);
  cursor: pointer;
  border-radius: 5px;
  align-items: center;
  margin: 1.4rem 1rem;
  padding: .5rem;
  font-size: 1.2rem;
  display: flex;
}

.chat-search__icon {
  width: 20px;
  height: 20px;
  margin-right: 1rem;
}

.chat-search__icon svg {
  width: 100%;
  height: 100%;
}

.chat-search__input {
  background: none;
  border: none;
  outline: none;
}

.chat-add {
  cursor: pointer;
  justify-content: center;
  align-items: center;
  padding: 0 1rem;
  display: flex;
}

.chat-add__icon {
  width: 22px;
  height: 22px;
  margin-right: 1.2rem;
}

.chat-add__text {
  font-size: 1.4rem;
  font-weight: 500;
}

.chats-list {
  flex-grow: 1;
  margin-top: 2rem;
  padding-right: .6rem;
  overflow: auto;
}

.chats-list::-webkit-scrollbar {
  width: 2px;
}

.chats-list::-webkit-scrollbar-track {
  background: var(--color-dark-bg);
}

.chats-list::-webkit-scrollbar-thumb {
  background-color: var(--color-primary);
  border-radius: 2px;
}

.chat {
  height: 100vh;
  flex-grow: 1;
  padding: 1rem 2rem;
  display: inline-block;
  overflow: hidden;
}

.chat-inactive {
  height: 100%;
  justify-content: center;
  align-items: center;
  font-size: 1.4rem;
  display: flex;
}

.chat-inactive__text {
  color: var(--color-light-text);
}

.chat-active {
  height: 100vh;
  flex-direction: column;
  display: flex;
}

.chat-user {
  border-bottom: 1px solid var(--color-dark-bg);
  align-items: center;
  padding-bottom: .5rem;
  display: flex;
}

.chat-user__profile {
  padding: 0;
  font-weight: bold;
}

.chat-user__menu {
  width: 25px;
  height: 25px;
  cursor: pointer;
  border-radius: 50%;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  display: inline-flex;
  position: relative;
}

.chat-user__menu:hover, .chat-user__menu.is-active {
  background-color: var(--color-dark-bg);
}

.chat-user__icon {
  display: flex;
}

.chat-user__actions {
  width: 210px;
  background-color: var(--color-light);
  opacity: 0;
  visibility: hidden;
  border-radius: 12px;
  padding: 2rem 1rem;
  font-size: 1.2rem;
  position: absolute;
  top: 200%;
  right: 0;
  box-shadow: 0 0 6px #00000024;
}

.chat-user__actions.is-active {
  opacity: 1;
  visibility: visible;
}

.chat-user__action {
  width: 100%;
  cursor: pointer;
  align-items: center;
  margin-bottom: 1rem;
  padding: .3rem;
  display: flex;
}

.chat-user__action:hover {
  background-color: var(--color-light-primary);
}

.chat-user__action:last-of-type {
  margin-bottom: 0;
}

.chat-user__action_remove {
  color: var(--color-error);
}

.chat-user__action-image {
  width: 22px;
  height: 22px;
  flex-shrink: 0;
  margin-right: 1rem;
}

.chat-user__action-text {
  font-weight: 500;
}

.chat-message {
  border-top: 1px solid var(--color-dark-bg);
  min-height: 62px;
  flex-shrink: 0;
  align-items: center;
  margin-bottom: 1rem;
  padding: 1rem 0 0;
  display: flex;
}

.chat-message__menu {
  width: 25px;
  height: 25px;
  cursor: pointer;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  padding: .1rem;
  display: inline-flex;
  position: relative;
}

.chat-message__menu:hover, .chat-message__menu.is-active {
  background-color: var(--color-dark-bg);
}

.chat-message__icon {
  display: inline-flex;
}

.chat-message__actions {
  width: 210px;
  background-color: var(--color-light);
  opacity: 0;
  visibility: hidden;
  border-radius: 12px;
  padding: 2rem 1rem;
  font-size: 1.2rem;
  position: absolute;
  inset: auto auto 200% 0;
  box-shadow: 0 0 6px #00000024;
}

.chat-message__actions.is-active {
  opacity: 1;
  visibility: visible;
}

.chat-message__action {
  width: 100%;
  cursor: pointer;
  align-items: center;
  margin-bottom: 1rem;
  padding: .3rem;
  display: flex;
}

.chat-message__action:hover {
  background-color: var(--color-light-primary);
}

.chat-message__action:last-of-type {
  margin-bottom: 0;
}

.chat-message__action-image {
  width: 22px;
  height: 22px;
  flex-shrink: 0;
  margin-right: 1rem;
}

.chat-message__action-text {
  font-weight: 500;
}

.chat-message__input {
  background: var(--color-dark-bg);
  border: none;
  border-radius: 30px;
  flex-grow: 1;
  margin-left: 1rem;
  padding: .6rem 2rem;
  display: block;
}

.chat-message__submit {
  width: 28px;
  height: 28px;
  cursor: pointer;
  margin-left: 1rem;
}

.chat-messages {
  flex-grow: 1;
  margin-bottom: 1rem;
  padding: 1rem 1rem 1rem 0;
  font-size: 1.4rem;
  overflow: auto;
}

.chat-messages::-webkit-scrollbar {
  width: 2px;
}

.chat-messages::-webkit-scrollbar-track {
  background: var(--color-dark-bg);
}

.chat-messages::-webkit-scrollbar-thumb {
  background-color: var(--color-primary);
  border-radius: 2px;
}

